import React, { useContext, useEffect, useState } from "react";
import { Etudes, search__archeive } from "src/assets/images";
import { i18n } from "../../i18n";
import Breadcrumb from "../shared/Breadcrumb";
import { Link } from "react-router-dom";
import styled from "styled-components";
import SearchService from "src/modules/Search/SearchService";
import Skeleton from "react-loading-skeleton";
import "react-loading-skeleton/dist/skeleton.css";
import SRPCard from "../Search/SRPCard";
import { BsBox } from "react-icons/bs";
import { ConfigProvider } from "antd";
import Pagination from "src/view/shared/table/Pagination";

function SearchArchive({ location }) {
  const langue = localStorage.getItem("language");

  const [publications, setPublications] = useState([]);
  const [count, setCount] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [loading, setLoading] = useState(false);

  const getValidThumbnail = (attachmentsArray) => {
    const acceptableExtions = "png, jpg, jpeg, gif";
    let fakeUrl = "https://via.placeholder.com/270x175";
    attachmentsArray.forEach((att) => {
      if (att.downloadUrl)
        if (att.downloadUrl.length >= 1) {
          const urlExtension = att.downloadUrl.substring(
            att.downloadUrl.lastIndexOf(".") + 1,
            att.downloadUrl.length
          );
          if (acceptableExtions.includes(urlExtension)) {
            fakeUrl = att.downloadUrl;
          }
        }
    });
    return fakeUrl;
  };
  const getArchivePublications = async (offset) => {
    const filter = location.archiveSearchString;
    const advanced = location.archiveFilter
      ? location.archiveFilter
      : { statut: "archive" };

    try {
      setLoading(true);
      let result = await SearchService.getArchivePublications(
        filter,
        "",
        10,
        offset,
        advanced
      );
      setPublications(result.rows);
      setCount(result.count);
      setLoading(false);
    } catch (error) {
      console.log("err", error);
      setLoading(false);
    }
  };
  useEffect(() => {
    getArchivePublications(0);
  }, []);

  return (
    <>
      <>
        <Breadcrumb
          title={i18n("common.Archives")}
          items={[[i18n("app.title"), "/"], [i18n("common.Archives")]]}
        />
      </>
      <div className="app__archieve__page_search">
        {loading ? (
          <section>
            <Skeleton height={170} />
          </section>
        ) : (
          <div className="archeive">
            <div className="archieve__content">
              <div className="archieve__left">
                <ConfigProvider
                  theme={{
                    token: {
                      colorPrimary: "#e1011a",
                    },
                  }}
                >
                  <SearchArchiveLayout>
                    <div>
                      {publications.length > 0 ? (
                        <>
                          <div>
                            {publications.map((searchResult, index) => {
                              let thumbnail: any;

                              if (searchResult["photos"]) {
                                thumbnail = getValidThumbnail(
                                  searchResult["photos"]
                                );
                              }
                              if (langue === "fr") {
                                return (
                                  <SRPCard
                                    type={
                                      searchResult["category"]
                                        ? searchResult["category"]["titleFR"]
                                        : null
                                    }
                                    title={searchResult["titleFR"]}
                                    entite={"detail"}
                                    key={index}
                                    _id={searchResult["_id"] ?? 0}
                                    date={searchResult["date"]}
                                    content={searchResult["descriptionFR"]}
                                    thumbnail={thumbnail}
                                  />
                                );
                              } else if (langue === "ar") {
                                return (
                                  <SRPCard
                                    type={
                                      searchResult["category"]
                                        ? searchResult["category"]["titleAR"]
                                        : null
                                    }
                                    title={searchResult["titleAR"]}
                                    entite={"detail"}
                                    key={index}
                                    _id={searchResult["_id"] ?? 0}
                                    date={searchResult["date"]}
                                    content={searchResult["descriptionAR"]}
                                    thumbnail={thumbnail}
                                  />
                                );
                              } else if (langue === "en") {
                                return (
                                  <SRPCard
                                    type={
                                      searchResult["category"]
                                        ? searchResult["category"]["titleEN"]
                                        : null
                                    }
                                    title={searchResult["titleEN"]}
                                    entite={"detail"}
                                    key={index}
                                    _id={searchResult["_id"] ?? 0}
                                    date={searchResult["date"]}
                                    content={searchResult["descriptionEN"]}
                                    thumbnail={thumbnail}
                                  />
                                );
                              }
                            })}
                          </div>

                          <Pagination
                            onChange={(e) => {
                              const offNumber = (e.current - 1) * e.pageSize;
                              getArchivePublications(offNumber);
                              setCurrentPage(e.current);
                            }}
                            disabled={loading}
                            pagination={{
                              current: currentPage,
                              pageSize: pageSize,
                              showSizeChanger: true,
                              total: count,
                            }}
                          />
                        </>
                      ) : (
                        <h3 className="h3" id="noDataFound">
                          <BsBox />
                          &nbsp;{i18n("common.noData")}
                        </h3>
                      )}
                    </div>
                  </SearchArchiveLayout>
                </ConfigProvider>
              </div>
            </div>
          </div>
        )}
      </div>
    </>
  );
}
export default SearchArchive;

const SearchArchiveLayout = styled.div`
  width: 100%;
  margin: 1rem 0;

  #noDataFound {
    font-family: "Proxima Nova";
    font-style: normal;
    font-weight: 100;
    font-size: 1.5rem;
    text-align: center;
    color: #c7c6c6;

    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
`;
